import {
  SETUP_POST_CREATE,
  SETUP_POST_EDIT,
  SETUP_POST_EDIT_SUCCESS,
  SETUP_POST_EDIT_ERROR
} from '../actions/postEditor';

const initialState = {
  postType: '',
  post: null,
  isNewPost: true,
  loading: 0,
  invalidated: false,
};

const defaultPost = {
  title: '',
  content: [],
};

function postEditor(state = initialState, action) {
  switch (action.type) {

    case SETUP_POST_CREATE:
      return {
        ...state,
        postType: action.postType,
        post: defaultPost,
        isNewPost: true,
        loading: 0,
        invalidated: false,
      };

    case SETUP_POST_EDIT:
      return {
        ...state,
        postType: action.postType,
        post: null,
        isNewPost: false,
        loading: state.loading + 1,
        invalidated: false
      };

    case SETUP_POST_EDIT_SUCCESS:
      return {
        ...state,
        post: action.post,
        loading: state.loading - 1,
      };

    case SETUP_POST_EDIT_ERROR:
      return {
        ...state,
        post: null,
        isNewPost: false,
        loading: 0,
        invalidated: true,
      };

    default:
      return state;
  }
}

export default postEditor;



